import React from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-scroll";
import styled from "styled-components";

const NavBar = styled(Navbar)`
	height: 80px;
	-webkit-box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, -9px -13px 3px -6px rgba(0,0,0,0); 
box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, -9px -13px 3px -6px rgba(0,0,0,0);
	
`;

const LinkStyled = styled(Link)`
	text-decoration: none;
	cursor: pointer;
	width: 120px;
	color: black;
	font-size: 100%;
	font-family:  monospace;


	&:hover {
		color: white;
		font-size: 110%;
	}
	@media (max-width: 768px) {
		display: none;
	}
`;

const Title = styled.h3`
	color: black;
	font-weight: bold;
	background-color: white;
	padding: 10px;
	cursor: pointer;
	border-radius: 15px 0 15px 0;
	border-bottom: 2px solid black;	
`;

export default function Header() {
	return (
		<NavBar bg="success" fixed="top">
			<Container className="d-flex flex-row">
				<Link
					to="intro-scroll"
					smooth={true}
					duration={1000}
					offset={-300}
				>
					<Title>Shacosu.js</Title>
				</Link>
				<Navbar.Toggle aria-controls="basic-navbar-nav" />
				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="ms-auto">
						<LinkStyled
							to="about-scroll"
							smooth={true}
							duration={1000}
							offset={-300}
						>
							<i className="fas fa-info-circle"></i> Sobre Mi
						</LinkStyled>

						<LinkStyled
							to="proyect-scroll"
							smooth={true}
							offset={-260}
							duration={1000}
						>
							<i className="fas fa-bookmark "></i> Proyectos
						</LinkStyled>
						<LinkStyled
							to="contact-scroll"
							smooth={true}
							offset={-250}
							duration={1000}
						>
							<i className="fas fa-envelope "></i> Contacto
						</LinkStyled>
					</Nav>
				</Navbar.Collapse>
			</Container>
		</NavBar>
	);
}
