import React from "react";
import { Container } from "react-bootstrap";
import styled from "styled-components";
import { projects } from "../proyects";

const Img = styled.img`
	width: 250px;
	height: 250px;
	margin: 0;
	padding: 0;
	cursor: pointer;
	border-radius: 20px 0 20px 0;
	border: 1px solid white;

	&:hover {
		opacity: 0.4;
	}
`;

const LinkStyled = styled.a`
	width: 250px;
	padding: 0;
	
`;

export default function Proyect() {
	return (
		<Container className="text-white h-100 mt-5" id="proyect-scroll">
			<div className="d-flex align-items-start justify-content-around ">
				<div className="row">
					{projects.map((project) => (
						<div className="d-flex flex-column w-25 mb-4">
						<h3 className="">{project.title}</h3>
						<LinkStyled
							href={project.url}
							target="_blank"
							rel="noopener noreferrer"
						>
							<Img
								src={project.img}
								alt={project.title}
							/>
							
						</LinkStyled>
						<p className="mt-2"><b className="border-bottom">Descripcion: </b> {project.description}</p>
						</div>
					))}
				</div>
			</div>
		</Container>
	);
}
